<template>
  <div class="content">
    <!-- <div class="title">采购意向</div>
    <div class="line"></div> -->
    <div class="picture">
      <img :src="img" alt>
    </div>
    <div class="productName">
      <span style="color:red;">*</span>
      <span>【采购设备名称】</span>
    </div>
    <div class="inputBox">
      <span style="color:#fff;">*1</span>
      <input type="text" placeholder="请输入设备名称" v-model="proname" readonly="readonly">
    </div>
    <div class="productName">
      <span style="color:red;">*</span>
      <span>【采购设备型号】</span>
    </div>
    <div class="inputBox">
      <span style="color:#fff;">*1</span>
      <input type="text" placeholder="请输入设备型号" v-model="promodel" readonly="readonly">
    </div>
    <div class="productName">
      <span style="color:red;">*</span>
      <span>【联系人】</span>
    </div>
    <div class="inputBox">
      <span style="color:#fff;">*1</span>
      <input type="text" placeholder="请输入您的姓名" v-model="contacts">
    </div>
    <div class="productName">
      <span style="color:red;">*</span>
      <span>【联系方式】</span>
    </div>
    <div class="inputBox">
      <span style="color:#fff;">*1</span>
      <input type="text" placeholder="请输入您的手机号" v-model="phone">
    </div>
    <div class="productName">
      <span style="color:red;">*</span>
      <span>【公司】</span>
    </div>
    <div class="inputBox">
      <span style="color:#fff;">*1</span>
      <input type="text" placeholder="请输入公司" v-model="company">
    </div>
    <div class="productName">
      <span style="color:#fff;">*</span>
      <span>【留言】</span>
    </div>
    <div class="inputBox">
      <span style="color:#fff;">*1</span>
      <textarea placeholder="请输入公司" v-model="message" maxlength="300"></textarea>
      <div class="num">{{message.length}}/300</div>
    </div>
    <div class="btn" @click="submit">提交</div>
  </div>
</template>

<script>
import { Toast } from "mint-ui";
export default {
  name: "HelloWorld",
  data() {
    return {
      img:'',
      proname: "",
      promodel: "",
      contacts: "",
      phone: "",
      company: "",
      message: ""
    };
  },
  created(){
    
    this.proname=this.$route.query.name
    var a=this.proname
    var a='B830'
    this.img=require("../assets/"+a+".png")
    this.promodel=this.$route.query.model
  },
  methods: {
    submit() {
      
      if (this.proname == "") {Toast({message: "请输入设备名称",position: "middle", duration: 2000 });return}
      if (this.promodel == "") {Toast({message: "请输入设备型号",position: "middle", duration: 2000 });return}
      if (this.contacts == "") {Toast({message: "请输入联系人",position: "middle", duration: 2000 });return}
      if (this.phone == "") {Toast({message: "请输入联系方式",position: "middle", duration: 2000 });return}
      if (this.company == "") {Toast({message: "请输入公司名称",position: "middle", duration: 2000 });return}
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
input {
-webkit-appearance: none;
}
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.content {
  width: 100%;
  height: 100vh;
  overflow: hidden;
  background: #fff;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  overflow: hidden;
}
.title {
  width: 100%;
  padding: 20px;
  font-size: 28px;
}
.line {
  width: 100%;
  height: 10px;
  background: #ccc;
}
.picture {
  width: 100%;
  text-align: center;
}
.picture > img {
  width: 70%;
  /* padding: 20px 0; */
}
.productName {
  box-sizing: border-box;
  /* border:1px solid red; */
  width: 90%;
  text-align: left;
  /* margin-top: 60px; */
}
.inputBox {
  box-sizing: border-box;
  /* border:1px solid red; */
  width: 90%;
  text-align: left;
  margin-top: 10px;
  position: relative;
}
.num {
  position: absolute;
  bottom: 20px;
  right: 60px;
}
input {
  width: 90%;
  height: 60px;
  border-top: 0px;
  border-left: 0px;
  border-right: 0px;
  outline: none;
  border-bottom: 1px solid #888;
}
.btn {
  width: 82%;
  background: #0368d0;
  border-radius: 6px;
  color: #fff;
  padding: 20px 0;
  font-size: 28px;
  margin: 30px 0;
}
textarea {
  padding: 10px;
  border-radius: 6px;
  width: 86%;
  height: 100px;
}
</style>



